.brandPage{
    margin-bottom: 70px;
    .brandContent{
        display: flex;
        justify-content: flex-start;
        padding: 70px 100px 97px;
        .brandDescription {
          text-align: start;
          margin-left: 100px;
          padding-top: 25px;
          .brand{
            color: var(--grey);
            font-family: var(--font-inter);
            font-size: 15px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            position: relative;
            bottom: 1px;
            left: 3px;
          }
          .brandName {
            color: var(--main-text-color);
            font-family: var(--font-inter);
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 1px;
            text-transform: uppercase;
            position: relative;
            top: 4px;
            left: 2px;
            h2{
                margin: 0;
              font-size: 30px;

            }

          }
          .brandItems {
            .item{
                display: inline-block;
                span{
                    padding-left: 12px;
                    color: var(--grey);
                    font-family: var(--font-inter);
                    font-size: 15px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: normal;
                }
            }
            .location{
                margin: 0 30px;
            }

          }
          .brandMessage{
            color: var(--main-text-color);
            font-family: var(--font-inter);
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 32.091px; /* 160.457% */
            opacity: 0.6;
            margin: 24px 0;
            padding-left: 2px;
          }


            .defaultBtn{
                background-image: url('../../assets/images/globeWhite.svg');
                background-repeat: no-repeat;
                background-position: 12% 50%;
                display: inline-block;
                margin-left: 0;
                margin-top: 41px;
                width: 146px;
            }


        }
    }



}

@import './MediaBrandPage.scss'
