@media screen and (max-width: 768px) {
  .collectionPage{
    .collection-page-wrapper {
      .collectionTitle {
        padding: 32px 0 0;
        position: relative;
        margin-bottom: 20px;

        .collectionName {
          margin-bottom: 6px;
          .name {
            span {
              font-size: 12px;
            }
          }
          .userAvatar {
            background-color: #D9D9D9;
            display: inline-block;
            margin-right: 10px;
            border-radius: 50%;
            opacity: 0.6;
            width: 22px;
            height: 22px;
          }
        }

        .pageTitle {
          display: flex;
          align-items: center;
          margin-bottom: 15px;

          .collectionName {
            h2 {
              font-size: 22px;
              font-weight: 700;
              letter-spacing: 1px;
              margin: 0;
            }
          }

          .copyLinkBtn {
            position: absolute;
            top: 40px;
            right: 0;
          }
        }

        .pageDescription {
          p {
            font-size: 16px;
            font-weight: 400;
            line-height: 29px;
            letter-spacing: 0.32px;
          }
        }
      }
    }
  }
}